
.slide-container {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.slide {
    width: 100%;
    height: 100vh;
    background-size: 100% 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url('../../public/images/hero-42171.jpg');
    justify-content: left;
    overflow: hidden; /* Used to mask content beyond the container */
    /*opacity: 1;*/
    /*transform: translateX(0%);*/
    /*transition: opacity 0.5s cubic-bezier(0.4, 0, 0.2, 1), transform 0.5s cubic-bezier(0.4, 0, 0.2, 1);*/
}
.slideUp{
    position: relative;
    z-index: 1;
    text-align: center;
    color: white; /* Set the font color :white */
    font-size: 80px; /* Set the font size and adjust it as needed */
}
.slideDown{
    position: relative;
    z-index: 1;
    margin-left: 23.5%;
    text-align: left;
    color: white; /* Set the font color :white */
    font-size: 40px; /* Set the font size and adjust it as needed */
}
.mask {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    /*background: linear-gradient(to bottom, transparent, #333); !* Add a gradient mask effect *!*/
    background-color: rgba(0, 0, 0, 0.5); /* Black mask, transparency can be adjusted as needed */
    z-index: 0; /* Set z-index to 0 to ensure the mask is underneath the content */
}
.labelButoon {
    display: block;
    color: white;
    font-size: 20px;
    text-align: center;
    text-decoration: none;
}
.nextDiv {
    margin-top: -45px;/* This is used to cover the extra white space after displaying the arc. */
    width: 100%;
    height: 320px;
    background-size: 100% 100%;
    justify-content: center;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 25px;
    overflow: hidden;
    position: relative;
    border-top-left-radius: 50px; /* Adjust to the arc size you want */
    border-top-right-radius: 50px; /* Adjust to the arc size you want */
    background-color: #fff;
}

.nextUp{
    font-size: 65px;
    width: 100%;  /* 填充整个宽度 */
    position: relative;
    color: #74a64D;
    justify-content: center;
    margin-left: 15%;
    padding-top: 8%;
    margin-right: 0;

}
.nextUp::after {
    margin-top: 5%;
    content: ''; /* 添加内容 */
    position: absolute; /* 使伪元素绝对定位 */
    top: 0;
    bottom: 0;
    height: 90%;
    width: 2px; /* 竖线宽度 */
    background-color: black; /* 竖线颜色 */
    left: 70%; /* 使竖线位于 div 中间 */
    transform: translateX(-50%); /* 通过 transform 属性使竖线水平居中 */
}
.nextDown{
    font-size: 28px;
    position: relative;
    text-align: left;
    padding-top: 12%;
}
.labelHero{
    padding-top: 15%;
}
.buttonDiv {
    display: flex;
    justify-content: center; /* 水平居中 */
}
.buttonHero {
    z-index: 1;
    display: flex;
    width: 17%;
    height: 80px;
    color: white;
    justify-content: center;
    text-align: center;
    line-height: 40px;
    margin: 0 10px;
    cursor: pointer;
    border: 2px solid white;
    align-items: center;
}
.buttonLabel{
    display: block; /* 将 label 设为块级元素 */
    color:white;
    font-size: 20px;
    text-align: center; /* 设置文字居中 */
}
@keyframes fadeInOut {
    0% {
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}
.slide-entering,
.slide-exiting {
    opacity: 0;
}
.slide-entered,
.slide-exited {
    opacity: 1;
}
.slide.visible {
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 1;
}
.mute-button {
    position: absolute;
    bottom: 0;
    right: 0;
    margin: 10px;
}
.prev {
    position: absolute;
    top: 50%;
    width: auto;
    padding: 16px;
    margin-top: -22px;
    font-size: 24px;
    font-weight: bold;
    cursor: pointer;
    left: 0;
}
.next {
    position: absolute;
    top: 50%;
    width: auto;
    padding: 16px;
    margin-top: -22px;
    font-size: 24px;
    font-weight: bold;
    cursor: pointer;
    right: 0;
}
