.requestDiv {
    width: 100%;
    background-size: 100% 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 25px;
    margin-left: -8%;
    overflow: hidden;
    position: relative;
    background-color: #fff;
}

