.tShirtBackGroundDiv {
    text-align: center;
    width: 100%;
    position: relative; /* 将位置设置为相对 */
    color: #3a5a40;
    background-size: 100% 100%;
    height: 100vh;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url('../../public/images/andy-bridge-ZwN8fS_slIY-unsplash.jpg');
    z-index: 0; /* 设置 z-index 为更低的值 */
}
.paymentDiv {
    text-align: center;
    width: 40%;
    color: #3a5a40;
    background-size: 100% 100%;
    height: 40px;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url('../../public/images/payment.png');
    z-index: 1;
}
.tShirtMask {
    margin-top: 90px;/* this is for navbar`s height */
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;/* this is same with  tShirtBackGroundDiv css`s height */
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1;
}
.overlay {
    position: absolute;
    top: 20%;
    left: 10%;
    width: 80%;
    height: 70%;
    background-color: white;
    z-index: 2;
    display: flex;
    /*justify-content: center;*/
    /*align-items: center;*/
    border: 2px solid white;
    border-radius: 40px;
    flex-direction: column;
}

.overlay > div {
    /* 悬浮内容的样式 */
}
.indicator {
    position: absolute;
    bottom: 10px; /* 距离底部的距离 */
    left: 50%; /* 水平居中 */
    transform: translateX(-50%); /* 水平居中 */
}

.indicator span {
    display: inline-block;
    width: 10px;
    height: 10px;
    border-radius: 50%; /* 使圆形 */
    background-color: #000;
    margin: 0 5px; /* 圆点之间的间距 */
    cursor: pointer;
}

/*.indicator span:hover {*/
    /*background-color: red;*/
/*}*/
.spanClick{
    background-color: red;
}
.TShirtTitle2{
    color: #69a662;
}

.TShirtTitle,
.TShirtDiv {
    margin-left: 3%;
    flex-direction: column; /* 设置为纵向排列 */
}

.TShirtTitle {
    margin-bottom: 20px; /* 标题与内容之间的间距 */
    color: #74a64D;
    font-size: 40px;
}

.TShirtDiv {
    width: 100%;
    margin-bottom: 20px; /* 内容之间的间距 */
}
.inputClass{
    display: inline-block;
    width:20%;
}
.inputClass1{
    display: inline-block;
    width:auto;
}
.tableCss{
    width: 80%;
}
.tdCssFlex {
    display: flex;
    flex: 0.22;
    align-items: center;
    padding-left: 10px;
}

.tdCss1 {
    width: 44%; /* 调整为您希望的宽度 */
    padding-right: 10px; /* 添加一些右边距 */
}

.tdCss{
    width: 44%;
    padding-left: 10px;
}
.buttonTShirtForm {
    z-index: 1;
    display: flex;
    margin-top: 20px;
    height: 40px;
    width: auto;
    color: white;
    justify-content: center;
    text-align: center;
    cursor: pointer;
    border: 2px solid white;
    align-items: center;
    background-color: #74a64D;
}

.inputClass3 {
    width: 65%; /* 下拉框的宽度 */
}
.inputClass2 {
    width: 40%; /* 下拉框的宽度 */
    padding: 10px; /* 下拉框的内边距 */
    font-size: 16px; /* 下拉框的字体大小 */
    border: 1px solid #ccc; /* 下拉框的边框样式 */
    border-radius: 5px; /* 下拉框的边框圆角 */
    background-color: #fff; /* 下拉框的背景色 */
    /*appearance: none; !* 隐藏默认的下拉箭头 *!*/
    cursor: pointer; /* 设置鼠标指针样式为指针 */
}

.inputClass2:hover {
    border-color: #999; /* 鼠标悬停时边框颜色变化 */
}

.inputClass2:focus {
    outline: none; /* 去除焦点时的边框样式 */
    border-color: #007bff; /* 获得焦点时的边框颜色变化 */
}
