.cards {
  padding: 4rem;
  background: #fff;
}

.cards > h1 {
  text-align: center;
  margin-bottom: 20px;
}

.cards__container {
  display: flex;
  flex-flow: column;
  align-items: center;
  max-width: 1120px;
  width: 90%;
  margin: 0 auto;
}

.cards__wrapper {
  position: relative;
  margin: 50px 0 45px;
}

.cards__items {
  margin-bottom: 24px;
}

.cards__item {
  display: flex;
  flex: 1;
  margin: 0 1rem;
  border-radius: 10px;
}

.cards__item__link {
  display: flex;
  flex-flow: column;
  width: 100%;
  box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
  -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  border-radius: 10px;
  overflow: hidden;
  text-decoration: none;
}

.cards__item__pic-wrap {
  position: relative;
  width: 100%;
  padding-top: 67%;
  overflow: hidden;
}

.fade-img {
  animation-name: fade-img;
  animation-duration: 2s;
}

.cards__item__pic-wrap::after {
  content: attr(data-category);
  position: absolute;
  bottom: 0;
  margin-left: 10px;
  padding: 6px 8px;
  max-width: calc((100%) - 60px);
  font-size: 12px;
  font-weight: 700;
  color: #fff;
  background-color: #1f98f4;
  box-sizing: border-box;
}

.cards__item__img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  object-fit: cover;
  transition: all 0.2s linear;
}

.cards__item__img:hover {
  transform: scale(1.1);
}

.cards__item__info {
  padding: 20px 30px 30px;
}
.cards__item__text {
   color: #252e48;
   font-size: 18px;
   line-height: 24px;
 }
.custom_cards__item__text {
  color: #252e48;
  font-size: 28px;
  line-height: 24px;
}
.custom_cards__item__title {
  color: #252e48;
  font-size: 14px;
  line-height: 24px;
}
.custom_cards__item__content{
  color: #252e48;
  font-size: 12px;
  line-height: 18px;

}
.custom_cards__item__info {
  padding: 10px 10px 10px;
  flex: 2;
}
.custom_cards__item__info_text {
  padding: 0px 10px 10px;
  flex: 2;
}
.custom_cards__item__button{
  font-size: 18px;
  width: 100%;
  background-color: #74A64D;
}
.custom_cards__item__button_div{
  padding:10px 10px 10px;
  width: 55%;
  flex: 1;
}
.custom_cards__item__pic-wrap {
  position: relative;
  width: 100%;
  padding-top: 67%;
  overflow: hidden;
}
.custom_cards__item__link {
  display: flex;
  flex-flow: column;
  width: 100%;
  box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
  -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  border-radius: 10px;
  overflow: hidden;
  text-decoration: none;
}
.labelH1{
  color: #C55636;
}
.cardDiv {
  display: flex;
  width: 75%;
}

.cardUp {
  flex: 1;
  text-align: right;
  font-size: 40px;
}

.cardDown{
  margin-left: 5%;
  flex: 0.8;
  text-align: center;
  height: auto;
  width: 20%;
  background-color: #74a64D;
  display: flex;
  justify-content: center; /* 水平居中 */
  align-items: center; /* 垂直居中 */
}
.cardButtonLabel{
  display: block; /* 将 label 设为块级元素 */
  color:white;
  font-size: 25px;
  text-align: center; /* 设置文字居中 */

}




  @media only screen and (min-width: 1200px) {
    .content__blog__container {
      width: 84%;
    }
  }
  
  @media only screen and (min-width: 1024px) {
    .cards__items {
      display: flex;
    }
  }
  
  @media only screen and (max-width: 1024px) {
    .cards__item {
      margin-bottom: 2rem;
    }
  }