
.registerInformationTitle,
.registerInformationDiv {
    flex-direction: column; /* 设置为纵向排列 */
}

.registerInformationTitle {
    margin-bottom: 20px; /* 标题与内容之间的间距 */
    color: #74a64D;
    font-size: 40px;
    margin-top: 3%;
    margin-left: 3%;
}
.registerInformationDiv {
    text-align: center;
    width: 100%;
    position: relative; /* 将位置设置为相对 */
    color: #3a5a40;
    background-size: 100% 100%;
    height: 100vh;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url('../../public/images/andy-bridge-ZwN8fS_slIY-unsplash.jpg');
    z-index: 0; /* 设置 z-index 为更低的值 */
}
.requesrInformationMask {
    margin-top: 90px;/* this is for navbar`s height */
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;/* this is same with  tShirtBackGroundDiv css`s height */
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1;
}
.overlayRegister {
    position: absolute;
    top: 15%;
    left: 10%;
    width: 85%;
    height: 80%;
    background-color: white;
    z-index: 2;
    display: flex;
    /*justify-content: center;*/
    /*align-items: center;*/
    border: 2px solid white;
    border-radius: 40px;
    flex-direction: column;
}

.overlayRegister > div {
    /* 悬浮内容的样式 */
}
.indicator {
    position: absolute;
    bottom: 10px; /* 距离底部的距离 */
    left: 50%; /* 水平居中 */
    transform: translateX(-50%); /* 水平居中 */
}

.indicator span {
    display: inline-block;
    width: 10px;
    height: 10px;
    border-radius: 50%; /* 使圆形 */
    background-color: #000;
    margin: 0 5px; /* 圆点之间的间距 */
    cursor: pointer;
}
