.donateDiv {
    display: flex;
    width: 75%;
    margin-left: 10%;
}

.donateTitle2{
    color: #69a662;
}

.donateTitle,
.donateDiv {
    margin-left: 3%;
    flex-direction: column; /* 设置为纵向排列 */
}

.donateTitle {
    margin-bottom: 20px; /* 标题与内容之间的间距 */
    color: #74a64D;
    font-size: 40px;
}
.donateLeft {
    flex: 0.8;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    text-align: left;
    font-size: 40px;
    height: 100%;
}

.placeholder {
    flex-grow: 1;
}
.donateCss1 {
    width: 44%;
    padding-right: 10px;
}
.donateCssFlex {
    display: flex;
    flex: 0.22;
    align-items: center;
    padding-left: 10px;
}
.donateInputClass2 {
    width: 40%;
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #fff;
    cursor: pointer;
}

.donateInputClass2:hover {
    border-color: #999;
}

.donateInputClass2:focus {
    outline: none;
    border-color: #007bff;
}
.donatePaymentDiv {
    text-align: center;
    width: 40%;
    color: #3a5a40;
    background-size: 100% 100%;
    height: 40px;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url('../../public/images/payment.png');
    z-index: 1;
}
.donateClass{
    display: inline-block;
    width:20%;
}
.donateClass1{
    display: inline-block;
    width:auto;
}
.donateInputClass1{
    display: inline-block;
    width:auto;
}
.donateInputClass3 {
    width: 55%; /* 下拉框的宽度 */
}
.donateRight {
    margin-left: 5%;
    flex: 1.2;
    text-align: center;
    width: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #3a5a40;
    background-size: 100% 100%;
    height: 50vh;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url('../../public/images/Dual Side T-shirt Artwork Mockup.png');
    justify-content: center;
}

.buttonDonate {
    z-index: 1;
    display: flex;
    margin-top: 20px;
    margin-left: -35%;
    height: 40px;
    width: 160px;
    color: white;
    justify-content: center;
    text-align: center;
    cursor: pointer;
    border: 2px solid white;
    align-items: center;
    background-color: #74a64D;
}

.labelDonate {
    display: block;
    color: white;
    font-size: 20px;
    text-align: center;
    text-decoration: none;
}

.labelTitle {
    color: #74a64D;
}
.donateClass2 {
    width: 70%;
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #fff;
}
.donateInputClass{
    display: inline-block;
    margin-left: 5%;
    width:25%;
}
.donateCss{
    width: 44%;
    padding-left: 10px;
}
.buttonDonateForm {
    z-index: 1;
    display: flex;
    margin-top: 20px;
    height: 40px;
    width: auto;
    color: white;
    justify-content: center;
    text-align: center;
    cursor: pointer;
    border: 2px solid white;
    align-items: center;
    background-color: #74a64D;
}

.donateLeftCss {
    flex: 1;
    /*text-align: right;*/
}
.donateRightCss{
    flex: 1;
    text-align: center;
    height: auto;
    width: 40%;
    display: flex;
    justify-content: left; /* 水平居中 */
    align-items: center; /* 垂直居中 */
}
.donateRightLabelCss{
    flex: 1;
    text-align: center;
    display: flex;
    color: gray;
    justify-content: left; /* 水平居中 */
    align-items: center; /* 垂直居中 */
}
.buttonDonateAndOrder {
    display: flex;
    margin-top: 20px;
    height: 40px;
    margin-left: 5%;
    width: 90%;
    color: white;
    justify-content: center;
    text-align: center;
    cursor: pointer;
    border: 2px solid white;
    align-items: center;
    background-color: #74a64D;
}