* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'PT Sans', sans-serif;
  }
  
  .home,
  .events,
  .about-us,
  .donate {
    display: flex;
    height: 90vh;
    align-items: center;
    justify-content: center;
    font-size: 3rem;
  }
  
  .events {
    background-image: url('../public/images/img-2.jpg');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    color: #fff;
    font-size: 100px;
  }
  
  .about-us {
    background-image: url('../public/images/img-3.jpg');
    background-position: center;
    background-size: fill;
    background-repeat: no-repeat;
    color: #fff;
    font-size: 100px;
    text-align: center;
  }
  
  .donate {
    background-image: url('../public/images/img-9.jpg');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    color: #fff;
    font-size: 100px;
  }
body, html {
  margin: 0%;
  padding: 0;
  width: 100%;
  height: 100%;
}
.home-container {
  position: relative;
  width: 100%;
  height: 100vh;
  margin: 0;
  padding: 0;
}

.video-container {
  position: relative; /* 将视频容器的定位属性改为相对定位 */
  width: 100%;
  height: 100%;
  display: flex; /* 使用 flex 布局 */
  justify-content: center; /* 在水平方向上居中 */
  align-items: center; /* 在垂直方向上居中 */
}

.video-bg {
  width: 100%; /* 将视频宽度设置为100% */
  height: auto; /* 让浏览器自动计算视频的高度 */
  max-width: 100%; /* 限制视频的最大宽度为容器的宽度 */
  max-height: 100%; /* 限制视频的最大高度为容器的高度 */
}




  @media screen and (max-width: 960px) {

    .hero-container > p {
      text-align: center;
    }

  }